* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html {
  scroll-behavior: smooth;
}

body {
  font-family: Raleway, sans-serif;
  overflow-x: hidden;
}

.main-container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 15px;
}

@media only screen and (width <= 1024px) {
  .main-container {
    padding: 0 20px;
  }
}

.header {
  z-index: 2;
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
}

.header.scrolled {
  background-color: #252b49;
}

.header.scrolled .header__container:after {
  width: 0;
}

.header.scrolled .header__link {
  padding: 20px 0;
}

@media only screen and (width <= 767px) {
  .header {
    padding: 20px 0;
  }
}

.header__container {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

@media only screen and (width <= 767px) {
  .header__container {
    padding-right: 35px;
  }
}

.header__container:after {
  content: "";
  border-top: 1px solid #babbc5;
  width: calc(100% - 30px);
  max-width: 1170px;
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
}

@media only screen and (width <= 1024px) {
  .header__container:after {
    width: calc(100% - 40px);
  }
}

@media only screen and (width <= 767px) {
  .header__container:after {
    display: none;
  }
}

.header__logo {
  z-index: 2;
  display: flex;
}

.header__logo svg {
  width: 184px;
  height: 27px;
}

@media only screen and (width <= 767px) {
  .header__logo svg {
    width: 100%;
    max-width: 103.472px;
    height: 100%;
  }

  .header__nav {
    z-index: 1;
    background: url("banner.886605a9.jpg") center / cover;
    width: 100%;
    height: 100vh;
    padding: 107px 20px;
    transition: left .3s linear;
    position: fixed;
    top: 0;
    left: 100%;
  }

  .header__nav:before {
    content: "";
    opacity: .8;
    background-color: #252b49;
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
  }

  .header__nav.open {
    left: 0%;
  }
}

.header__nav-list {
  display: flex;
}

@media only screen and (width <= 767px) {
  .header__nav-list {
    flex-direction: column;
  }
}

.header__toggle-menu {
  z-index: 2;
  width: 24px;
  height: 18px;
  display: none;
  position: relative;
}

@media only screen and (width <= 767px) {
  .header__toggle-menu {
    align-items: center;
    display: flex;
    position: fixed;
    right: 34.53px;
  }
}

.header__toggle-menu:before {
  content: "";
  border-bottom: 3px solid #fff;
  width: 100%;
  transition: all .3s linear;
  display: block;
  position: absolute;
  top: 0;
  left: 0;
}

.header__toggle-menu:after {
  content: "";
  border-bottom: 3px solid #fff;
  width: 100%;
  transition: all .3s linear;
  display: block;
  position: absolute;
  bottom: 0;
  left: 0;
}

.header__toggle-menu.open .header__toogle-item {
  width: 0;
}

.header__toggle-menu.open:after {
  transform: rotate(-45deg)translate(5px, -5px);
}

.header__toggle-menu.open:before {
  transform: rotate(45deg)translate(5.8px, 5.8px);
}

.header__toogle-item {
  border-bottom: 3px solid #fff;
  width: 100%;
  transition: all .3s linear;
  position: absolute;
  top: 50%;
  left: 0;
  transform: translateY(-50%);
}

.header__list-item {
  flex: auto;
  list-style: none;
}

@media only screen and (width <= 767px) {
